import React from 'react';import PropTypes from 'prop-types';import { SearchButton } from './StyledComponents';const Button = ({ text, onClick }) => {  const handleClick = () => {    onClick();  };  return (    <div>      <SearchButton type="button" onClick={handleClick}>        {text}      </SearchButton>    </div>  );};Button.propTypes = {  text: PropTypes.string.isRequired,  onClick: PropTypes.func.isRequired,};export default Button;